@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

body {
  font-family: "Roboto", sans-serif;
  font-weight: 450;
  font-style: normal;
  font-size: large;
  margin: 0;
  padding: 0;
  background: #4776e6;
  background: -webkit-linear-gradient(90deg, #4776e6 0%, #8e54e9 100%);
  background: linear-gradient(90deg, #4776e6 0%, #8e54e9 100%);
  color: #ffffff;
}

* {
  box-sizing: border-box;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

main {
  width: 100%;
  max-width: 1175px;
  padding: 20px;
}

header,
main {
  width: 100%;
}

section {
  width: 100%;
  padding: 20px;
  margin: 20px 0;
}

h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 800;
  font-style: normal;
  font-size: 60px;
  color: #ffffff;
}

h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 50px;
  font-style: normal;
  color: #ffffff;
  margin-bottom: 20px; /* Adds space below the heading */
}

h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: large;
  color: #ffffff;
}

p {
  font-family: "Roboto", sans-serif;
  font-size: 25px;
  color: #ffffff;
  max-width: 550px; /* Adjust as needed */
  margin: 0 auto; /* Centers the paragraph horizontally */
  text-align: justify;
}

/* Media queries for responsive design */
@media (max-width: 768px) {
  h1 {
    font-size: 40px;
  }

  h2 {
    font-size: 35px;
  }

  h3 {
    font-size: 25px;
  }

  p {
    font-size: 18px;
    max-width: 100%;
    padding: 0 10px;
  }

  section {
    padding: 10px;
    margin: 10px 0;
  }

  main {
    padding: 10px;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 30px;
  }

  h2 {
    font-size: 25px;
  }

  h3 {
    font-size: 20px;
  }

  p {
    font-size: 16px;
  }

  section {
    padding: 5px;
    margin: 5px 0;
  }

  main {
    padding: 5px;
  }
}
