header {
  position: static; /* Ensure the header behaves normally in the document flow */
  top: 0;
  left: 0;
  width: 100%;
  background-color: transparent; /* Transparent background for the header */
  color: white; /* Text color */
  padding: 20px 0; /* Padding for some spacing */
  z-index: 1000; /* Ensures the header is above other elements */
}

nav ul {
  list-style-type: none; /* Remove default list style */
  margin: 0;
  padding: 0;
  display: flex; /* Flexbox for horizontal layout */
  justify-content: center; /* Center the navigation items */
}

nav ul li {
  margin: 0 15px; /* Spacing between the navigation items */
}

.NavButton {
  background: none;
  color: white;
  border: none;
  box-shadow: none;
  font-size: 25px;
  font-weight: normal;
  border-radius: 1.5rem;
  box-sizing: border-box;
  cursor: pointer;
  display: inline-block;
  font-family: "Roboto", sans-serif;
  line-height: 1;
  padding: 1rem 1.6rem;
  text-align: center;
  text-decoration: none #0d172a solid;
  text-decoration-thickness: auto;
  transition: all 0.1s cubic-bezier(0.4, 0, 0.2, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  padding: 1rem 2rem;
  width: 200px;
}

.NavButton:hover {
  background-color: #0f114e;
}
