#about {
  text-align: center; /* Centers the content horizontally */
  padding-bottom: 50px;
}

.about-content {
  display: flex;
  justify-content: center; /* Centers items horizontally */
}

.about-text {
  flex: 1; /* Take up remaining space */
  padding-right: 20px; /* Adjust spacing between text and image */
  font-size: 27px;
}

.about-image {
  width: 350px; /* Adjust width of the image container */
  height: 350px; /* Adjust height of the image container */
  margin-left: 20px; /* Adjust margin between text and image */
}

h2 {
  padding-bottom: 0;
  margin-bottom: 0;
}
.pText {
  font-size: 25px;
  text-align: justify;
  text-justify: inter-character;
}
