.contact-links {
  display: flex;
  flex-direction: row;
  justify-content: center; /* Center-align links horizontally */
  align-items: center; /* Center-align links vertically */
  margin-top: 15px; /* Optional: Add margin to separate from section content */
}

.contact-links a {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center-align icon and text vertically */
  text-decoration: none;
  color: inherit;
  margin: 0 10px; /* Adjust horizontal spacing between links */
  font-size: 20px; /* Adjust icon size */
}

.contact-links a:hover {
  color: #0f114e; /* Optional: Change color on hover */
}
